//import hooks
import React, {useEffect, useRef, useState} from 'react';
import emailjs from 'emailjs-com'


export default function Contact () {

    // Déclaration des références
    const contactForm = useRef();
    const senderName = useRef();
    const senderEmail = useRef();
    const subject = useRef();
    const message = useRef();
    const warningPar = useRef();

    //Déclaration des states/états
    const [warning, setWarning] = useState({error:true, warningTxt:""});

    //Équivalent de ComponentDidMount()
    useEffect(() => {
        //Liaison des références à leurs éléments physiques
        // senderName.current && senderName.current.focus();
        // senderEmail.current && senderEmail.current.focus();
        // subject.current && subject.current.focus();
        // message.current && message.current.focus();
        // warningPar.current && warningPar.current.focus();
        // contactForm.current && contactForm.current.focus();
    }, [])
    
    // Vérifie si l'utilisateur a cliqué au moins une fois dans une case donnée
    let confirmFirstSelect = (e) => {
        if (e.target.getAttribute("data-clicked")==="false") {
            e.target.setAttribute("data-clicked", "true")
        }
    } //Fin de la fonction confirmFirstSelect

    // Function qui gère la validation des cases d'entrées
    let formValidation = (e) => {

        warningPar.current.style.backgroundColor = "transparent"

        //Validation du nom
        if (e.target.name==="user_name" && e.target.getAttribute("data-clicked")==="true") {
            let name = senderName.current;

            //Si la case du nom est vide ou contient des caractères inhabituels
            if ((!(/[^$\s]/.test(name.value)))) 
            {
                name.placeholder = "Please enter your name";
                name.className = "input-warning";
            } 
            else if ( (/[^A-Za-zÀ-ÖØ-öø-ÿ\-'\s.]/.test(name.value)) ) 
            {
                name.placeholder = "Please enter a name without special characters";
                name.className = "input-warning";
            } 
            else 
            {
                name.className = "input-correct";
            }
        }

        //Validation de l'email
        if (e.target.name==="user_email" && e.target.getAttribute("data-clicked")==="true") {

            let email = senderEmail.current;

            //Vérifie si l'email est vide ou n'est pas valide selon certains critères
            if ( !(/[a-z0-9]+([-+._][a-z0-9]+){0,2}@.*?(\.(a(?:[cdefgilmnoqrstuwxz]|ero|(?:rp|si)a)|b(?:[abdefghijmnorstvwyz]iz)|c(?:[acdfghiklmnoruvxyz]|at|o(?:m|op))|d[ejkmoz]|e(?:[ceghrstu]|du)|f[ijkmor]|g(?:[abdefghilmnpqrstuwy]|ov)|h[kmnrtu]|i(?:[delmnoqrst]|n(?:fo|t))|j(?:[emop]|obs)|k[eghimnprwyz]|l[abcikrstuvy]|m(?:[acdeghklmnopqrstuvwxyz]|il|obi|useum)|n(?:[acefgilopruz]|ame|et)|o(?:m|rg)|p(?:[aefghklmnrstwy]|ro)|qa|r[eosuw]|s[abcdeghijklmnortuvyz]|t(?:[cdfghjklmnoprtvwz]|(?:rav)?el)|u[agkmsyz]|v[aceginu]|w[fs]|y[etu]|z[amw])\b){1,2}/.test(email.value)) ) 
            {
                email.placeholder = "Please enter a valid email"
                email.className = "input-warning";
            }
            else 
            {
                email.className = "input-correct";
            }
        }

        //Validation du sujet
        if (e.target.name==="subject" && e.target.getAttribute("data-clicked")==="true") {
            let sub = subject.current;

            //Vérifie si le sujet est vide
            if (!(/[^$\s]/.test(sub.value))) {
                sub.placeholder = "Please enter your subject"
                sub.className = "input-warning";
            }
            else {
                sub.className = "input-correct";
            }
        }

        //Validation du message
        if (e.target.name==="message" && e.target.getAttribute("data-clicked")==="true") {

            let msg = message.current;

            // Vérifie si le message est vide
            if (!(/[^$\s]/.test(msg.value))) {
                msg.placeholder = "Please enter your message"
                msg.className = "input-warning";
            }
            else {
                msg.className = "input-correct";
            }
        }

        // ==================================================== //
        //Vérifie s'il y a au moins une erreur dans toutes les cases

        const isFalse = (currentValue) => currentValue === "input-warning";
        const fieldClassArray = [senderName.current.className, senderEmail.current.className, subject.current.className, message.current.className];
                // console.log("At least one is false? "+ fieldClassArray.some(isFalse))
        
        if (fieldClassArray.some(isFalse)) {
            setWarning({error:true})//Si c'est le cas, la propriété bool de l'état 'warining' est changé pour true
        }
        else {
            setWarning({error:false})//Sinon, la propriété bool de l'état 'warining' est changé pour false
        }
        
    } //Fin de la fonction formValidation

    function sendEmail(e) {
        e.preventDefault();

        //Changement de couleur du message d'avertissement
        if (warning.warningTxt !== "" || warning.warningTxt !== " ") {
            warningPar.current.style.backgroundColor = "rgba(255, 255, 255, 0.651)";
            warningPar.current.style.color = "#32afa9";

            if (warning.error === false) {
                warningPar.current.style.color = "#32afa9";
            } else {
                warningPar.current.style.color = "#feb377";
            }
        }
            
        //Prévalidation

        //array.some(condition) méthode retournant un bool dépendamment de si tous les éléments d'une tableau répondent ou non à une condition donnée
        const isUnchanged = (currentValue) => currentValue === "false"; //la condition
        const fieldClickedArray = [senderName.current.getAttribute("data-clicked"), senderEmail.current.getAttribute("data-clicked"), subject.current.getAttribute("data-clicked"), message.current.getAttribute("data-clicked")]; //le tableau

        if (fieldClickedArray.some(isUnchanged)) 
        {
            //Si certaines cases sont vident, il y a une erreur et le msg d'alerte change
            setWarning({error:true})
            setWarning({warningTxt:"Please fill all the required fields"});
        }
        else if (warning.error === true) 
        {
            //Si certaines cases sont fautives, il y a une erreur et le msg d'alerte change
            setWarning({warningTxt:"You need to fill all the required fields properly"});
        }
        else if (warning.error === false) //Si il n'y a pas d'erreur
        {
            e.target.contact_number.value = Math.random() * 100000 | 0; //generation d'un numero de contact
            //l'API Email JS envoie un message
            emailjs.sendForm('ra_gmail', 'contact_form', e.target,'user_R4uMMxPYmLhGaZZMSLwYE')
                .then((result) => {
                    console.log(result.text);
                    setWarning({warningTxt:"Thanks. Your message got sent."});
                }, (error) => {
                    console.log(error.text);
                    setWarning({warningTxt:"Sorry, there was a mistake. Try again Later."});
                });
        }
        
    } //Fin de la fonction sendEmail
   
    
    return (
        <div className="main-section-content-contact">

            <div className="form-container">

                    <form ref={contactForm} className="contact-form" onSubmit={sendEmail}>
                    <p className="warning-msg" ref={warningPar}>{warning.warningTxt}</p>

                        <input type="hidden" name="contact_number"/>

                        <label htmlFor="name">Your Name</label>
                        <input ref={senderName} type="text" id="name" name="user_name" data-clicked="false" onClick={confirmFirstSelect} onBlur={formValidation} maxLength="50" placeholder="Your name..."/>

                        <label htmlFor="email" >Your Email</label>
                        <input ref={senderEmail} type="text" id="email" name="user_email" data-clicked="false" onClick={confirmFirstSelect} onBlur={formValidation} maxLength="254" placeholder="Your email..."/>

                        <label htmlFor="subject">Subject</label>
                        <input ref={subject} type="text" id="subject" name="subject" data-clicked="false" onClick={confirmFirstSelect} onBlur={formValidation} maxLength="50" placeholder="The subject of your message"/>

                        <label htmlFor="msg" >Message</label>
                        <textarea ref={message} id="msg" name="message" data-clicked="false" onClick={confirmFirstSelect} onBlur={formValidation} maxLength="340"  placeholder="Write something.." style={{height:200}}></textarea>

                        <input className="submitBtn" value="Send" type="submit"/>
                    </form>

            </div>
           
        </div>

    );
}