import React, {useEffect, useRef, useState} from 'react';
import { TweenMax, gsap } from 'gsap';
import SmoothScroll from '../layout/SmoothScroll'
import Loading from '../layout/Loading'
import ReactHtmlParser from 'react-html-parser'; 
import HtmlParser from 'react-html-parser';

export default function SingleProject (props) {

    // Déclaration des références
    const projectDiv = useRef();
    const projectInfoSection = useRef();
    const projectCloseBtn = useRef();
    const projectSeeMoreBtn = useRef();
    const loadingDiv = useRef();
    const oneProjectChildren = useRef();
    const projectCtn = useRef();

    const lightboxMenuItems = useRef();
    const lightboxPreviousPic = useRef();
    const lightboxPic = useRef();
    const lightboxNextPic = useRef();

    //Déclaration des états/props
    const [load, setLoad] = useState(true); //État de la fenetre de rechargement du projet
    const setSingleProject = props.singleProjectState[1]; //Prend l'état de l'ouverture de la fenêtre du projet
    const thisProject = props.projectData; //Prend toutes les données du projet

    const projectImgs = HtmlParser(thisProject.medias);

    //États qui sauvegarde l'id de l'image sélectionnée
    const [selectionnedImage, setSelectionnedImage] = useState ({
        ID: 0
    })
    //État qui sauvegarde le stade du lightbox
    const [lightboxState, setLightboxState] = useState ({
        open: false,
        nightmode: false,
    })
    //État qui sauvegarde le stade du lightbox
    const [wheelState, setWheelState] = useState ({
        didScroll: false
    })



    //Equivalent de componentDidMount()
    useEffect(() => {
        //Liaison des références à leurs éléments physiques
        // projectDiv.current && projectDiv.current.focus();
        // projectInfoSection.current && projectInfoSection.current.focus();
        // projectCloseBtn.current && projectCloseBtn.current.focus();
        // projectSeeMoreBtn.current && projectSeeMoreBtn.current.focus();
        // loadingDiv.current && loadingDiv.current.focus();
        // oneProjectChildren.current && oneProjectChildren.current.focus();
        // projectCtn.current && projectCtn.current.focus();


        // lightboxMenuItems.current && lightboxMenuItems.current.focus();
        // lightboxPreviousPic.current && lightboxPreviousPic.current.focus();
        // lightboxPic.current && lightboxPic.current.focus();
        // lightboxNextPic.current && lightboxNextPic.current.focus();

        //Empêche gsap (GreenSock TweenMax) d'envoyer des avertissements
        gsap.config({
            nullTargetWarn: false
        })

        //Animations à l'ouverture du projet
        TweenMax.fromTo(projectDiv.current, 0.8, {y:"-150%"}, {y:"0%"} );
        TweenMax.fromTo(loadingDiv.current, 0.3, {opacity:0}, {opacity:1, delay:1} );
        TweenMax.fromTo(projectDiv.current, 0.3, {backgroundColor:"rgba(255,255,255,0.5)"}, {backgroundColor:thisProject.themeColorP, delay:1} );

        //Fonction appelé à la fin de vie du composant
        return () => {
            console.log("Component Unmounted");
        }
    },[thisProject])

    // Fonction qui gère le chargement du projet
    let handleLoaded = () => {
        TweenMax.fromTo(loadingDiv.current, 0.3, {opacity:1}, {opacity:0} );
        setTimeout(()=>{setLoad(false)},400)
        setTimeout(()=>{
            TweenMax.fromTo(oneProjectChildren.current, 0.3, {opacity:0}, {opacity:1} );
            new SmoothScroll(projectInfoSection.current,120,5);
        }, 400)
    } //Fin de la fonction handleLoaded

    // Gère la fermeture de la fenêtre du projet
    let handleComeOut = () => {
        TweenMax.fromTo(projectDiv.current, 0.5, {y:"0%"}, {y:"100%"} );
        setTimeout(()=>{setSingleProject({showProjectComponent: false});}, 500);
    }

    // Gère l'ouverture et la fermeture de la division contenant les détails du projet
    let handleSwipe = () => {

        if (projectInfoSection.current.getAttribute("data-state") === "closed") {
            TweenMax.fromTo(projectInfoSection.current, 0.5, {x:"0%"}, {x:"-100%"} );
            projectInfoSection.current.setAttribute("data-state","open");
            projectCloseBtn.current.style.filter = "invert(1)";
            projectSeeMoreBtn.current.style.filter = "invert(1)";
            projectSeeMoreBtn.current.style.backgroundColor = "rgba(0,0,0,0.5)";
            projectCloseBtn.current.style.backgroundColor = "rgba(0,0,0,0.5)";
        }
        else if (projectInfoSection.current.getAttribute("data-state") === "open") {
            TweenMax.fromTo(projectInfoSection.current, 0.5, {x:"-100%"}, {x:"0%"} );
            projectInfoSection.current.setAttribute("data-state","closed");
            projectCloseBtn.current.style.filter = "invert(0)";
            projectSeeMoreBtn.current.style.filter = "invert(0)";
            projectSeeMoreBtn.current.style.backgroundColor = "transparent";
            projectCloseBtn.current.style.backgroundColor = "transparent";
        }
    }
    //close the lightbox
    let handleCloseLightbox = (e) => {
        setLightboxState({open: false})
    }

    //Fonction qui gère le déroulement du menu d'images dans le lightbox
    let handleLightBoxBenuClick = (e, dir) => {

        let currentScrollPos = lightboxMenuItems.current.scrollLeft;
        let sliderWidth = lightboxMenuItems.current.offsetWidth;
        
        if (dir === "right") {
            lightboxMenuItems.current.scrollTo({
                left:currentScrollPos+(sliderWidth - (sliderWidth/1.5)),
                behavior: 'smooth'
            });
        } else {
            lightboxMenuItems.current.scrollTo({
                left:currentScrollPos-(sliderWidth - (sliderWidth/1.3)),
                behavior: 'smooth'
            });
        }
    }

    //Fonction qui gère l'image affichée en gros dans le lightbox
    let handleDisplayedImage = (index) => {

        if (lightboxPic.current.getAttribute('data-img') !== index) {

            TweenMax.fromTo(lightboxPic.current, 0.1, {opacity:1}, {opacity:0} );
        
            setTimeout(() => {
                setSelectionnedImage({ID: index})
                TweenMax.fromTo(lightboxPic.current, 0.3, {opacity:0}, {opacity:1, delay:0.2} );
               
               lightboxMenuItems.current.children[index].scrollIntoView(true, {behavior:"smooth"});
            }, 100);
        }

    }

    //Changement d'image avec la roulette
    let handleLightboxScroll = (e) => {

        let scrollDir = e.deltaY;

        if (wheelState.didScroll === false) {

            setWheelState({didScroll: true});

            if (scrollDir < 0) {
            
                if (selectionnedImage.ID !== 0) {
                    handleDisplayedImage(selectionnedImage.ID - 1)
                } else {
                    handleDisplayedImage(projectImgs.length-1);
                }
            } else if (scrollDir > 0) {
    
                if (selectionnedImage.ID !== projectImgs.length-1) {
                    handleDisplayedImage(selectionnedImage.ID + 1)
                } else {
                    handleDisplayedImage(0);
                }
            }
            setTimeout(() => {
                setWheelState({didScroll: false});
            }, 500);

        }
       
    }

    let handleClickOnGallery = (index) => {
        setSelectionnedImage({ID: index});
        setLightboxState({open:true});
    }

   
    // Composant conditionnel 'CollabList'
    function CollabList() {

        //Si la liste de collaborateur est plus haut que 0, du HTML est retourné, sinon, on retourne rien
        if (thisProject.collabs.length > 0) {
            return (
                <div className="project-collab-list">
                    <p>made in collaboration with</p>
                    <ul>
                        {thisProject.collabs.map( (collabName, index) => (<li key={index} >{collabName}</li>))}
                    </ul>
                </div>
            )
        }
        else {
            return false;
        }
        
    }

    return  (
        <div ref={projectDiv} className="one-project"  >
            {
                load ?
                <div ref={loadingDiv}><Loading/></div> :
                null
            }
            <div ref={projectCloseBtn} className="project-close" onClick={handleComeOut}></div>
            <div ref={projectSeeMoreBtn} className="project-see-more" onClick={handleSwipe}></div>
            <div className="one-project-container" ref={oneProjectChildren}>


            {/* project main view */}
            <section className="project-mainview" >
                {/* titles at the top left */}
                <div className="project-mainview-title-div">
                    <h4 className="project-mainview-title">{thisProject.title}</h4>
                    <h5 className="project-mainview-category">{thisProject.category}</h5>
                </div>

                {/* Conteneur du projet - Vue principale */}
                {/* <div className="project-mainview-container"   ref={projectCtn}> */}
                    {/* Transforme la ligne json de type string en HTML */}
                    <div className="main-media" onLoad={handleLoaded} onPlay={handleLoaded}>{ReactHtmlParser (thisProject.project)}</div>
                {/* </div> */}
            </section>
            
            {/* lightbox */}
            <div className={"light-box "+ (lightboxState.nightmode ? "nightmode" : "") } style={{ visibility: lightboxState.open===false ? 'hidden' : 'visible', opacity: lightboxState.open===false ? 0 : 1 }}>

                <div className="lightbox-header">
                    <p className="counter">{selectionnedImage.ID+1}/{projectImgs.length}</p>
                    <img className="nightmode-btn" onPointerDown={ () => { (lightboxState.nightmode ? setLightboxState({nightmode: false}) : setLightboxState({nightmode: true}) ) } } src="/portfolio/media/img/night-icon2.svg" alt="night icon"/>
                    <p onPointerDown={handleCloseLightbox} className="close-lightbox">x</p>
                </div>

                <div className="pic-frame" ref={lightboxPic} onWheel={ handleLightboxScroll } data-img={selectionnedImage.ID}>
                    <div className="scroll-instructions"  onPointerEnter={(e)=>{e.currentTarget.classList.add("hidden")}} ><p>Scroll here to change the displayed image</p><img src="/portfolio/media/img/mouse_icon.svg" alt="mouse icon" /></div>
                    {projectImgs[selectionnedImage.ID]}
                </div>

                <aside className="lightbox-pics-menu" onWheel={ (e) => { if(e.deltaY>0){handleLightBoxBenuClick( e, 'left')}else{handleLightBoxBenuClick( e, 'right')} }}>
                    <p className="lightbox-menu-btn"  onPointerDown={ (e)=>{  if (selectionnedImage.ID!== 0){handleDisplayedImage(selectionnedImage.ID - 1)}else{handleDisplayedImage(projectImgs.length-1) } }}>&lt;</p>
                    <div className="lightbox-menu-items" ref={lightboxMenuItems}>
                        {
                            projectImgs.map((image, index) => (<div onPointerUp={() => { handleDisplayedImage(index) } } key={index} style={{ opacity: selectionnedImage.ID===index ? 0.5 : 1 }} >{image}</div>) )
                        }
                    </div>
                    <p className="lightbox-menu-btn" onPointerDown={ (e)=>{  if (selectionnedImage.ID !== projectImgs.length-1) { handleDisplayedImage(selectionnedImage.ID + 1)} else {handleDisplayedImage(0);}  } }>&gt;</p>
                </aside>


            </div>
            
            {/* Section des détails du projet */}
            <section className="project-details" ref={projectInfoSection} data-state="closed">
                <div className="project-details-container" >
                    <div className="project-info-title-div" style={{backgroundColor:thisProject.themeColorP, backgroundImage:'url('+thisProject.thumbnail+')'}}><h4 className="project-info-title">{thisProject.title}</h4></div>
                    <div className="project-info-div">
                        {/* Génération conditionnelle de la liste de collaborateurs */}
                        <CollabList/>
                        
                        <div className="project-info-desc-container">
                            {
                                thisProject.description.split('\n').map( (par, i) => ( <p key={i} className="project-info-desc">{par}</p> ))
                            }
                        </div>

                        <section className='project-pic-container'>
                        {
                        <div className="first-pic">{projectImgs[0]}</div>
                        }
                        <div className="gallery">
                            <div>Click a pic to open the gallery &gt;</div>
                            {
                                projectImgs.map( (media, index) => ( <div key={index} onPointerDown={ (e)=>{handleClickOnGallery(index)} } >{media}</div> ))
                            }
                        </div>
                        </section>
                        
                        {/* <div className="project-info-img-container" >{thisProject.medias.map( (media, index) => (<div className="project-info-img" key={index}>{ ReactHtmlParser (media) }</div>))}</div> */}
                    </div>
                </div>
                
            </section>

            </div>
            
        </div>
    );

    
}