import React from 'react';
import { Link } from 'react-router-dom';


export default function Error() {

    return (
        <div className="error-page">
            <p>This page does not exist</p>
            <img src={process.env.PUBLIC_URL+"/media/img/404.svg"} alt="404"/>
            <p className="safe-space-link"><Link to={process.env.PUBLIC_URL}>Click <span>here</span> to go back to the main page</Link></p>
        </div>
    );
}



 
