//Importation des Hooks et Modules
import React, {useEffect, useState, useRef} from 'react';

//Importation du fichier json contenant tous les projets
import projectsData from "./../../data/projects.json"

// Importation des composants
import SingleProject from '../layout/SingleProject';


export default function Project (props) {

    // Déclaration des références
    const previewWindow = useRef();
    const movingLine = useRef();
    // Déclaration des états

    //États qui sauvegarde l'id du projet cliqué et s'il est ouvert ou non
    const [singleProject, setSingleProject] = useState ({
        showProjectComponent: false,
        selectedProjectId: null
    });


    //Equivalent de componentDidMount()
    useEffect(() => {
        
        //Liaison des références à leurs éléments physiques
        // previewWindow.current && previewWindow.current.focus();
        // movingLine.current && movingLine.current.focus();

        //À travers le changement de page, des données ont été transféré
        if (props.location.state != null) {

            if (props.location.state.projectId != null) {
                //Si la propriété "projectId" existe, c'est que l'utilisateur a cliqué sur un projet
                //présent dans la section "récent" de la page d'accueil. Il faut sauvegarder son id et ouvrir ce projet
                setSingleProject({showProjectComponent:true, selectedProjectId: props.location.state.projectId})
            }
        }

        window.scrollTo(0,0);

    }, [props.location.state])

    window.onscroll = (e) => {
        //Convertit les pixel en pourcentage
        let bodyScrollHeight = document.body.scrollHeight - window.innerHeight;
        let scrollYPerc = Math.round((window.scrollY * 100)/bodyScrollHeight);
        movingLine.current.style.backgroundPositionY = (window.scrollY/2) + "px";
    }

    let handleClick = (e) => {
        //Si le visiteur a cliqué sur un projet
        if (e.currentTarget.parentNode.className === "project-item") {
            let index = e.currentTarget.parentNode.getAttribute('data-key'); //On prend l'id du projet cliqué
            setSingleProject({showProjectComponent: true, selectedProjectId: index}); //On le sauvegarde dans l'état "singleProject"
        }
    }

    let handleHover = (e) => {
        let id = e.currentTarget.getAttribute('data-key');
        let themeColor = projectsData.projects[id].themeColorP;
        let imgUrl = projectsData.projects[id].thumbnail;
        previewWindow.current.style.backgroundImage = 'url(' + imgUrl + ')';
        previewWindow.current.style.opacity = 1;
        e.currentTarget.style.backgroundColor = themeColor;
    }

    let handleLeave = (e) => {
        e.currentTarget.style.backgroundColor = 'transparent';
        previewWindow.current.style.opacity = 0;
    }

    return (
        
        <div className="main-section-content-project" id="project">
            
            {
                singleProject.showProjectComponent ?
                <SingleProject singleProjectState={[singleProject,setSingleProject]} projectData={projectsData.projects[singleProject.selectedProjectId]}/> :
                null
            }
            <h1>My work</h1>
            <section className="all-projects">
                <div className="projects">
                    <div ref={movingLine} className='projects-aside-section'>
                
                    </div>
                    <div className="projects-list-container">
                         <div className="project-preview" ref={previewWindow}></div>
                        <ul>
                        {
                            projectsData.projects.map( project => (<li className="project-item" key={project.id} data-key={project.id} onPointerLeave={handleLeave} onPointerOver={handleHover} ><span onPointerDown={handleClick} className="project-preview-title">{project.title}</span> <span className="project-preview-category">{project.category}</span></li>))
                        }
                        </ul>
                    </div>
                </div>
            </section>
           
        </div>

    );
}

