// Importation des Hooks et des Modules
import React from 'react';
import { Switch, Route, useLocation} from 'react-router-dom';
import { TransitionGroup, CSSTransition } from 'react-transition-group'

// Importation des composants
import Home from '../pages/Home';
import Project from '../pages/Project';
import Error from '../pages/Error'

export default function MainSection(props) {

    // Déclaration des méthodes externes
    const location = useLocation();

    return (
        
        <section className="Main-section">

            {/* TransitionGorup et CSSTransition permettent la transition entre les pages */}
            {/* liée au fichier '_transition.scss qui contient des extensions de la classe '.transition */}
            <TransitionGroup className="transition-group">
                <CSSTransition
                key={location.key}
                timeout={{ enter: 1500, exit: 1000 }}
                classNames="transition"
                >
                <div className="main-section-content route-section page-padding ">

                    <Switch location={location}>

                        <Route exact path={props.navOptions[0].opLink} component={Home}></Route>

                        <Route exact path={props.navOptions[1].opLink} component={Project}></Route>

                        <Route path="*" component={Error}/>
                        
                    </Switch>
                </div>

                </CSSTransition>
            </TransitionGroup>
        </section>
    );
}
