import React, {useRef, useEffect} from 'react';
import { Link } from 'react-router-dom'

export default function Menu(props) {

    const menuChkBox = useRef();
    const nav = useRef();

    //Fonction appelée au chargement du composant (équivalent de ComponentDidMount)
    useEffect(() => {
        //Permet aux refs (références) de se connecter à leurs éléments respectifs et d'être accessible
        menuChkBox.current && menuChkBox.current.focus();
        nav.current && nav.current.focus();
    },[])

    let handleClick = () => {
        setTimeout( ()=> {
            //Ferme le menu sur mobile lorsqu'une option est cliqué
            menuChkBox.current.checked = false;
            
        },500)
    }
// onPointerDown={handleClick(option.opLink, option.optionTitle)}
    return (
        <nav className="nav" ref={nav}>
            <div className="nav-header">
                <Link to={{pathname:props.navOptions[0].opLink}}><div className="nav-logo"></div></Link>
                <label id="btnNav" htmlFor="chkNav" className="nav-icon"></label>
            </div>
            <input hidden id="chkNav" type="checkbox" ref={menuChkBox}/>
            <ul className="nav-list">
                {
                    props.navOptions.map( option => (<li key={option.id} onPointerDown={handleClick} className='nav-option'><span className="nav-option-title" ><Link to={{pathname:option.opLink, state:{project:null,scrollToDiv:option.scrollToDiv}}}>{option.optionTitle}</Link></span></li>))
                }
                <li className="nav-option"><span className="nav-option-title">reb.anescar@gmail.com</span></li>
                <li className="cnt-icon" onPointerDown={()=>{window.open("https://www.linkedin.com/in/rebecca-anescar-7b97a0196/")}}><i className="fab fa-linkedin-in"></i> LinkedIn</li>
                {/* <li className="cnt-icon" onPointerDown={()=>{window.location.href="https://www.artstation.com/"}}><i className="fab fa-artstation"></i> ArtStation</li> */}
                {/* <li className="cnt-icon" onPointerDown={()=>{window.location.href="https://www.behance.net/"}}><i className="fab fa-behance"></i>Behance</li> */}
            </ul>
        </nav>
    );
}



 
