//import hooks
import React, {useEffect, useRef} from 'react';

export default function Loading (props) {


    // create the refs
    const loadAnim = useRef();

    //Equivalent of componentDidMount
    useEffect(() => {
        //do focus of refs
        loadAnim.current && loadAnim.current.focus();
        return () => {
        }
    }, []);

    
    return (
        <div className="LoadPage">
            <div ref={loadAnim} className="loading-animation"></div>
        </div>
    );
}