// Importation des Hooks et des Modules
import React, {useState, useEffect, useRef} from 'react';
import { BrowserRouter as Router} from 'react-router-dom';
import { TweenMax } from 'gsap';
import SmoothScroll from './components/layout/SmoothScroll';

// Importation des composants
import Menu from './components/layout/Menu';
import MainSection from './components/layout/MainSection';
import Loading from './components/layout/Loading';

// Importation des feuilles de style
import './css/normalize.css'
import './css/style.css'

export default function App() {

  // Déclaration des références
  const loadingDiv = useRef();
  const customCursor = useRef();

  // Déclaration des États
  const [load, setLoad] = useState(true); // Enregistre l'état de la fenêtre de chargement

  const [navOptions] = useState ([
    {
        id: "1",
        optionTitle: "Home",
        opLink: process.env.PUBLIC_URL + "/",
        scrollToDiv: 'home'
    },
    {
        id: "2",
        optionTitle: "My projects",
        opLink:  process.env.PUBLIC_URL + "/projects",
        scrollToDiv: 'project'
    }
  ]); //Enregistre les informations des options du menu


  // Internet Explorer 6-11
  const isIE = /*@cc_on!@*/false || !!document.documentMode;
  // Edge 20+
  const isEdge = !isIE && !!window.StyleMedia;

   //Equivalent of componentDidMount/componentDidUpdate/componentWillUnmount
  useEffect(() => {
    //Liaison des références à leurs éléments physiques
    loadingDiv.current && loadingDiv.current.focus();
    customCursor.current && customCursor.current.focus();

    window.addEventListener("load", handleLoaded());// Lorsque la page est chargée, appelle une fonction

    console.log(isIE)
    console.log(isEdge)

    new SmoothScroll(document,120,10); //Instance qui permet un défilement plus fluide de la page

  },[isEdge, isIE])

  let handleLoaded = () => {
    // Si la page est chargée, la fenêtre de chargement est levée
    TweenMax.fromTo(loadingDiv.current, 0.5, {translateY:0}, {translateY:"-100%", delay:0.5} );
    setTimeout(()=>{setLoad(false); }, 1100);
  }

  // Gère la position du curseur personnalisé
  let handleMouseMove = (e) => {
    customCursor.current.style.left = e.clientX+"px";
    customCursor.current.style.top = e.clientY+"px";
  }
  // Dépendamment d'où est le curseur, son style change (agit comme un ':hover' css)
  let handleMouseOver = (e) => {
    // console.log(e.target.children )
    if (
      e.target.parentNode.parentNode.className === "recent-work-box" ||
      e.target.parentNode.className === "submitBtn" ||
      e.target.parentNode.className === "project" ||
      // e.target.parentNode.children[0].className === "project-item" || //ADDED 2020-09-22
      e.target.parentNode.className === "contact-list" ||
      e.target.parentNode.classList.contains("lightbox-header") ||
      e.target.parentNode.className === "see-project-p"
    ) 
    {
      customCursor.current.style.width = "30px";
      customCursor.current.style.height = "30px";
      customCursor.current.children[0].style.backdropFilter = "invert(1) saturate(0)"
      customCursor.current.children[0].style.borderColor = "transparent"
    }
    else if (e.target.parentNode.className === "nav-option-title") 
    {
      customCursor.current.style.width = "30px";
      customCursor.current.style.height = "30px";
      customCursor.current.children[0].style.backdropFilter = "invert(1) saturate(0)";
      customCursor.current.children[0].style.borderColor = "transparent"
    }
    else if (e.target.className === "submitBtn") 
    {
      customCursor.current.style.width = "30px";
      customCursor.current.style.height = "30px";
    }
    else if (e.target.className === "canva-MatterJS" || 
            e.target.classList.contains("nightmode") ||
            e.target.parentNode.classList.contains("nightmode") ||
            e.target.parentNode.parentNode.classList.contains("nightmode") ||
            e.target.parentNode.parentNode.classList.contains("lightbox-menu-items"))
    {
      // customCursor.current.children[0].style.borderColor = "white"
      customCursor.current.style.width = "10px";
      customCursor.current.style.height = "10px";
      customCursor.current.children[0].style.backdropFilter = "invert(0) saturate(1)"
      customCursor.current.children[0].style.borderColor = "white"
    }
    else 
    {
      customCursor.current.style.width = "10px";
      customCursor.current.style.height = "10px";
      customCursor.current.children[0].style.backdropFilter = "invert(0) saturate(1)"
      customCursor.current.children[0].style.borderColor = "black"
    }
    
  }

  return (
    <Router component={App} >
      <div className="App" onMouseMove={handleMouseMove} onPointerMove={handleMouseMove} onMouseOver={handleMouseOver}>
        {
          load ?
          <div ref={loadingDiv} className="app-loading-page"><Loading/></div> :
          null
        }

        {/* The menu */}
        <Menu navOptions={navOptions}/>

        <div ref={customCursor} className="custom-cursor"><div></div></div>

        {/* The main sections that contains all other pages */}
        <MainSection navOptions={navOptions}/>

      </div>
    </Router>
  );
}
